const ObjectUtil = {
  areObjectKeys: (obj = {}) => {
    return Object.keys(obj).length;
  },
  getKeyValue: obj => {
    if (!ObjectUtil.areObjectKeys(obj)) {
      return;
    }
    let key = Object.keys(obj)[0];
    let value = obj[key];
    return {
      key,
      value
    };
  }
};

module.exports = ObjectUtil;
